import { Stomp } from './stomp.js'
import UniWebSocket from './uniWebSocket.js'
import { wsUrl, refreshToken } from './config/config.js'

/**
 * WebSocket stomp 
 */
class Ws {

	constructor() {
		// 
		this.reconnectInterval = 50000
		// debug
		this.debug = true
		// ws
		if (wsUrl.indexOf('http://') === 0) {
			this.url = wsUrl.replace('http://', 'ws://')
		} else if (wsUrl.indexOf('https://') === 0) {
			this.url = wsUrl.replace('https://', 'wss://')
		} else {
			this.url = wsUrl
		}
		// stomp
		this.client = null
		// 
		this.reconnectId = null
		// 
		this.subscribes = {}
	}

	/**
	 * 
	 */
	connect() {
		// 
		if (this.client && this.client.connected) {
			return
		}
		// tokenSec-WebSocket-Protocol
		// client.connectheader
		// token
		let token = getApp().globalData.thirdSession
		let ws = new UniWebSocket(this.url)
		this.client = Stomp.over(ws)
		// debug
		if (!this.debug) {
			this.client.debug = () => {
				console.log(ws)
			}
		}
		this.client.connect({

				token:getApp().globalData.thirdSession
			
		},
			frame => {
				// 
				Object.keys(this.subscribes).forEach(key => {
					this.subscribe(key, this.subscribes[key].callback)
				})
			},
			error => {
				// 
				this.reconnectId = setTimeout(() => {
					this.reconnect()
				}, this.reconnectInterval)
			}
		)
		// WebSocketstompconnect
		ws.onerror = () => {
			// 
			this.reconnectId = setTimeout(() => {
				// token
				refreshToken().then(() => {
					this.reconnect()
				})
			}, this.reconnectInterval)
		}
	}

	/**
	 * 
	 */
	reconnect() {
		// false
		Object.keys(this.subscribes).forEach(key => {
			this.subscribes[key]['subscribed'] = false
		})
		// 
		this.connect()
	}

	/**
	 * 
	 */
	disconnect() {
		// 
		if (this.client) {
			this.client.disconnect()
		}
		// 
		if (this.reconnectId) {
			clearTimeout(this.reconnectId)
			this.reconnectId = null
		}
		// 
		this.subscribes = {}
	}
	
	/**
	 * 
	 * @param {Object} destination 
	 * @param {Object} callback 
	 */
	subscribe(destination, callback) {
		if (this.subscribes[destination] && this.subscribes[destination]['subscribed']) { // 
			return
		} else if (this.client && this.client.connected) { // 
			let subscribe = this.client.subscribe(destination, res => callback(res))
			this.subscribes[destination] = { callback: callback, subscribed: true, subscribe: subscribe }
		} else { // 
			this.subscribes[destination] = { callback: callback, subscribed: false }
		}
	}
	
	/**
	 * 
	 * @param {Object} destination 
	 */
	unsubscribe(destination) {
		if (this.subscribes[destination]) {
			// 
			this.subscribes[destination].subscribe.unsubscribe()
			// 
			delete this.subscribes[destination]
		}
	}
	
	/**
	 * 
	 * @param {Object} destination 
	 * @param {Object} message 
	 */
	send(destination, message) {
		console.log(destination)
		console.log(message)
		if (this.client) {
			console.log(this.client)
			this.client.send(destination, null, message)
		}
	}
}

export default new Ws()
